import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppService } from 'src/app/app.service';

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.scss']
})
export class SettingComponent implements OnInit {
  settingFrom: FormGroup;
  openFile: boolean = false;
  constructor(public app: AppService) { }
  ngOnInit() {
    this.settingFrom = new FormGroup({
      name: new FormControl(this.app.user.name, Validators.required),
      username: new FormControl(this.app.user.username, Validators.required),
      password: new FormControl(this.app.user.password, Validators.required),
      img: new FormControl(""),
    })
  }
  saveSeeting() {
    if (!this.settingFrom.valid) {
      this.app.log("بعض البيانات مفقودة")
      return;
    }
    this.app.wait();
    this.app.http.put(`/api/users/${this.app.user.id}`, this.settingFrom.value).subscribe(
      res => {
        this.app.router.navigateByUrl("dashboard");
        this.app.user = {
          ...this.app.user,
          ...this.settingFrom.value
        }
        sessionStorage.setItem("lawerAssistant", JSON.stringify(this.app.user));
        this.app.wait(false);
      }, err => { }
    )
  }
}